* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "MuseoModerno", cursive;
  color: white;
}

body {
  background: url(https://images.unsplash.com/photo-1591101060011-a08535113bbe?ixlib=rb-1.2.1&q=85&fm=jpg&crop=entropy&cs=srgb&ixid=eyJhcHBfaWQiOjE0NTg5fQ) no-repeat center center/cover;
  text-align: center;
  background-color: #001f3f;
  background-blend-mode: multiply;
  min-height: 100vh;
}

h1 {
  color: white;
}

#drum-machine {
  width: 400px;
  height: 400px;
  margin: 20px auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5px;
  opacity: 0.8;
  background: #0da574;
  border-radius: 10px;
  padding: 10px;
}

.drum-pad {
  width: 70px;
  height: 70px;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2em;
  background: #083358;
  cursor: pointer;
  justify-self: center;
  align-self: center;
  color: white;
  box-shadow: 0 10px 20px #f9ff21;
}

.drum-pad:active {
  box-shadow: unset;
}

.toggle {
  isolation: isolate;
  position: relative;
  height: 30px;
  width: 60px;
  border-radius: 15px;
  overflow: hidden;
  border: 1px groove white;
}

.indicator {
  height: 100%;
  width: 200%;
  background: #ecf0f3;
  border-radius: 15px;
  transform: translate3d(-75%, 0, 0);
  transition: transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);
}

.toggle:active~.indicator {
  transform: translate3d(25%, 0, 0);
}

.label {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  color: #eee;
  justify-content: center;
  margin: 10px auto;
  width: 260px;
}

.label-text {
  margin-left: 16px;
}

.toggle {
  isolation: isolate;
  position: relative;
  height: 30px;
  width: 60px;
  border-radius: 15px;
  overflow: hidden;
  margin-right: 10px;
}

.toggle-state {
  display: none;
}

.indicator {
  height: 100%;
  width: 200%;
  background: #ecf0f3;
  border-radius: 15px;
  transform: translate3d(-75%, 0, 0);
  transition: transform 0.4s cubic-bezier(0.85, 0.05, 0.18, 1.35);
}

.toggle-state:checked~.indicator {
  transform: translate3d(25%, 0, 0);
}

#display {
  background: #1a936f;
  width: 200px;
  margin: 20px auto;
  padding: 10px;
}

.other {
  display: flex;
  flex-direction: column;
  margin: 25px 30px;
}

@media only screen and (max-width: 600px) {
  #drum-machine {
    width: 300px;
    height: 320px;
    margin: 50px auto;
  }
  .drum-pad {
    width: 57px;
    height: 57px;
    font-size: 1.5em;
  }
}